<template>
  <div class="landing-page">
    <modal ref='modal' :data='selected'/>
    <div class="divider s2"/>
    <div class="container">
      <container class="dark" title="Projects">
        <div class="columns is-mobile is-multiline padding">
          <project @open='openProject' v-for='(project, i) in projects' :key="i" :data='project' 
            class="column is-12-mobile is-6-tablet is-3-desktop"/>
        </div>
      </container>
    </div>
  </div>
</template>

<script>

import container from "@/components/structure/Section.vue"
import project from "@/components/structure/Project.vue"
import Modal from "@/components/structure/ProjectModal.vue"
import Projects from "@/assets/data/projects.js"
 
export default {
  name: 'About',
  components: {
    container,
    project,
    Modal
  },
  data() {
    return {
      projects: Projects,
      selected: {}
    }
  },
  methods: {
    openProject(project) {
      this.selected = null
      this.selected = project
      this.$refs['modal'].open();
    }
  }
}
</script>

